.gpt3__blog__article-container {
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  background: var(--color-footer);
}

.gpt3__blog__article-image {
  width: 100%;
  height: 100%;
  background: var(--color-footer);
}

.gpt3__blog__article-image img {
  width: 100%;
  height: 100%;
}

.gpt3__blog__article-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  padding: 1rem 1.5rem;
  height: 100%;
}

.gpt3__blog__article-content p {
  font-family: var(--font-family);
  font-weight: 700;
  font-size: 12px;
  line-height: 35px;
  color: #FFFFFF;
}

.gpt3__blog__article-content p:last-child {
  cursor: pointer;
}

.gpt3__blog__article-content h3 {
  font-family: var(--font-family);
font-weight: 800;
font-size: 25px;
line-height: 30px;
color: #FFFFFF;
margin-bottom: 5rem;
cursor: pointer;
}

@media screen and (max-width:550px) {
  .gpt3__blog__article-content h3 {
    font-size: 18px;
    line-height: 25px;
  }
}